.exp {
  position: relative;
  grid-area: exp;
  &__pe-title {
    margin: 23px 0 14px 52px;
    font-size: 13px;
    font-weight: bold;
    text-align: left;
    padding: 3px 0 5px;
    color: #41414a;
    letter-spacing: 0.8px;
    text-transform: uppercase;
    border-bottom: 2px solid #80808091;
  }

  &__pe-description {
    font-size: 11px;
    height: 17px;
    letter-spacing: 0.7px;
    margin: 0 0 0 77px;
    padding-bottom: 6px;
    text-align: left;
  }

  &__pe-title-1 {
    padding-bottom: 10px;
    margin-top: 33px;
  }

  &__accent-gray {
    width: 122px;
    height: 4px;
    background-color: #828282;
    position: absolute;
    left: 52px;
    top: 28px;
  }

  &__accent-gray2 {
    top: 127px;
  }

  &__skillset {
    font-size: 12px;
    text-align: left;
    margin-left: 174px;
    border-left: 2px solid #cecece;
  }

  &__item-wrap {
    line-height: 23px;
    position: relative;
    left: -98px;
  }

  &__item-wrap6 {
    top: 2px;
    height: 40px;

    & .exp__item {
      top: -6px;
    }
  }

  &__item-wrap7 {
    height: 35px;
    top: 4px;

    & .exp__item {
      top: -11px;
    }
  }

  &__item {
    font-weight: bold;
    font-size: 12px;
    position: absolute;
    width: 98px;
    border-bottom: 2px solid #cecece;
  }

  &__hr {
    width: 213px;
    background-color: #cecece;
    border: none;
    color: #cecece;
    background-color: #cecece;
    height: 2px;
    position: absolute;
    left: 97px;
    bottom: -8px;
  }

  &__skill {
    display: flex;
    text-align: left;
    padding: 0 5px 0 103px;
    font-size: 11px;
    align-items: baseline;
  }

  &__item-last {
    top: 23px;
  }
}
