.projects {
  display: none;
}

.App {
  text-align: center;
  font-family: "Prompt", sans-serif;
  background: url("/img/gazon.jpg");
  padding-top: 1vh;
  min-height: 100vh;
  padding: 2vh 0;
  box-sizing: border-box;
}

.flex-wrap {
  background: url("/img/fon.jpg");
  display: flex;
  position: relative;
  max-width: 1280px;
  width: 90%;
  margin: auto;
  box-shadow: 0px 0px 18px 2px rgba(0, 0, 0, 0.75);
  border-radius: 10px;
  box-sizing: border-box;
  padding-bottom: 15px;
}

.pen {
  background: url("/img/pen.png");
  width: 15px;
  height: 115px;
  background-size: 66%;
  background-repeat: no-repeat;
  position: absolute;
  left: 216px;
  top: 390px;
  transform: rotate(2deg);
}

.cup {
  background: url(/img/cup.png);
  width: 127px;
  height: 174px;
  position: absolute;
  right: 85px;
  top: 95px;
  background-size: 215px;
  background-position: -45px -23px;
  background-repeat: no-repeat;
}

.iphone {
  background-image: url("/img/i-keys.png");
  width: 145px;
  background-color: transparent;
  height: 284px;
  position: absolute;
  right: 14px;
  top: 500px;
  transform: rotate(-25deg);
  background-size: 100.5%;
  background-repeat: no-repeat;
  border-radius: 21px;
  box-shadow: 2px 1px 5px 2px rgba(0, 0, 0, 0.36);
}

.heft {
  background-image: url("/img/heft2.png");
  width: 180px;
  height: 270px;
  position: absolute;
  left: 16px;
  top: 223px;
  transform: rotate(-3deg);
  background-size: 100.5%;
  background-repeat: no-repeat;
}

.projects {
  left: 65px;
  width: 180px;
  height: 90px;
  position: absolute;

  &__container {
    width: 200px;
    height: 250px;
    position: absolute;
  }

  &__getImg {
    position: absolute;
    z-index: 2;
    left: 38px;
    top: -92px;
    width: 86px;
    display: block;
  }

  &__getImgContainer {
    position: absolute;
    height: 100%;
    width: 100%;
    backface-visibility: hidden;
  }

  &__getFromGit {
    background: url(/img/visitki/get.jpg);
    width: 178px;
    background-size: 100%;
    box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.45);
    height: 84px;
    border: none;
    position: absolute;
    top: 161px;
    left: -9px;
    z-index: 2;
    outline: none;
    cursor: pointer;
    opacity: 1;
    transition: transform 0.3s, opacity 0.3s 0.5s, z-index 0.3s 1.5s;
    outline: none;

    &:hover {
      transform: rotate(-12deg);
    }

    &--disabled {
      opacity: 0;
      z-index: -1;
    }
  }

  &__getImg {
    top: 166px;
    opacity: 0;
    z-index: -1;
  }

  &__getImg-react_uber-eats {
    transition: opacity 0.01s 1s, z-index 0.3s 2s, top 0.4s;
  }

  &__getImg-portfolio_layout_dia {
    transition: opacity 0.01s 1s, z-index 0.3s 2s, top 0.4s 0.05s;
  }

  &__getImg-portfolio_layout_miami {
    transition: opacity 0.01s 1s, z-index 0.3s 2s, top 0.4s 0.1s;
  }

  &__project-main {
    background: url("/img/projects-main.jpg");
    box-sizing: border-box;
    width: 170px;
    height: 93px;
    position: absolute;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;
    padding-top: 57px;
    text-transform: uppercase;
    font-size: 11px;
    color: #442e27;
    left: 59px;
    top: 394px;
    box-shadow: 1px -1px 1px -1px rgba(0, 0, 0, 1);
  }

  &__project1 {
    background-color: #fff;
    box-sizing: border-box;
    width: 170px;
    height: 99px;
    position: absolute;
    padding-top: 10px;
    text-transform: uppercase;
    font-size: 11px;
    color: #442e27;
    left: 59px;
    top: 364px;
    box-shadow: 4px 3px 2px -1px rgba(0, 0, 0, 0.1);
  }
}

@font-face {
  font-family: "Neverwinter";
  src: url("/fonts/Neverwinter.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Russell";
  src: url("/fonts/Russell.otf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@import url("https://fonts.googleapis.com/css?family=Prompt:400,400i,700&display=swap");
@import url("https://fonts.googleapis.com/css?family=Cormorant+Garamond:400,500,600,700&display=swap");
@import url("https://fonts.googleapis.com/css?family=Orbitron:400,500,600,700,800,900&display=swap"); //font-family: 'Orbitron', sans-serif;

@import "./media.scss";
